import React from 'react';
import './sidebar.scss'
import SidebarSongCom from "../SidebarSongCom/SidebarSongCom";

const SidebarCom = ({songArray}) => {
    return (
        <div className="wrapper">
            <h1>Libary</h1>

            {
                songArray.map(el=>{
                  return <SidebarSongCom el={el}/>
                })
            }
        </div>
    );
};

export default SidebarCom;