import React, {useState, useRef} from 'react';
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faPlay, faPause, faForward, faBackward} from "@fortawesome/free-solid-svg-icons";
import "./player.scss"

const PlayerCom = ({currentSong, setCurrentSong, songArray, isPlaying, setIsPlaying}) => {
    let audio = useRef("");
    let input = useRef('');

function handleClick(){
    setIsPlaying(!isPlaying)
    if(isPlaying){
        audio.current.pause();
    }else {
        audio.current.play();
    }
}



    return (
        <div>
            <div className="player">
                <div className="time-control">
                    <p> </p>
                    <form action="/">
                        <label htmlFor="musicWeb">
                        </label>
                        <input id="musicWeb" type="range" ref={input}>
                        </input>
                    </form>
                    <p> </p>
                </div>
                <div className="controller">
                    <FontAwesomeIcon size="2x" icon={faBackward}/>
                    <FontAwesomeIcon onClick={handleClick} size="2x" icon={faPlay}/>
                    <FontAwesomeIcon size="2x" icon={faForward}/>
                </div>
                <audio ref={audio} src={currentSong.audio}> </audio>
            </div>
        </div>
    );
};

export default PlayerCom;