import React from 'react';
import './sidebarSongCom.scss';

const SidebarSongCom = ({el}) => {
    return (
        <div>
            <div className="row">
                <img className="songImg" src={el.cover} alt="Photo"/>
                <div>
                    <h1>{el.name}</h1>
                    <h2>{el.artist}</h2>
                </div>
            </div>
        </div>
    );
};

export default SidebarSongCom;