import React, {useState} from "react";
import data from "./data";
import SongCom from "./components/SongCom/SongCom";
import SidebarCom from "./components/SidebarCom/SidebarCom";
import PlayerCom from "./components/PlayerCom/PlayerCom";


function App() {
    const [songArray, setSongArray] = useState(data);
    const [currentSong, setCurrentSong] = useState(songArray[0])
    const [isPlaying, setIsPlaying] = useState(false)
    const [openLibary, setOpenLibary] = useState(false)
    return (
        <div className="App">
            <SongCom Song={currentSong}/>
            <SidebarCom songArray={songArray}/>
            <PlayerCom isPlaying={isPlaying} setIsPlaying={setIsPlaying} currentSong={currentSong} setCurrentSong={setCurrentSong} songArray={songArray}/>
        </div>
    );
}

export default App;
